define('search/entities/extract_proxy', [
    'require',
    'exports',
    'module',
    'backbone'
], function (require) {
    var Backbone = require('backbone');
    var Reference = Backbone.Model.extend({ defaults: { title: '' } });
    var ReferenceCollection = Backbone.Collection.extend({ model: Reference });
    var ExtractProxyModel = Backbone.Model.extend({
        defaults: function () {
            return {
                manuscript: new Reference(),
                author: '',
                sourceId: null,
                sourceLineRef: null,
                normalized: '',
                original: '',
                speakers: new ReferenceCollection(),
                playwrights: new ReferenceCollection()
            };
        },
        getExtract: function () {
            if (!this._repo) {
                throw new Error('no repository specified');
            }
            return this._repo.get(this.id);
        },
        setRepo: function (repo) {
            this._repo = repo;
        }
    });
    return {
        ExtractProxy: ExtractProxyModel,
        ExtractProxyCollection: Backbone.Collection.extend({ model: ExtractProxyModel })
    };
});